








































































































































































































import Vue from 'vue';

import { PersonData } from '../interfaces/ipeople';
import { peopleModule } from '@/store/modules/people';
import { UserImage, imageModule } from '../store/modules/images';
import { ApplicationData } from '../interfaces/generic';
import { acModule } from '../store/modules/acs';

export default Vue.extend({
  name: 'Setup',
  
  data: () => ({
    valid: false,
    name: '',
    nameRules: [
      (v: string) => !!v || 'Name is required',
      (v: string) => (v && v.length <= 20) || 'Name must be less than 20 characters',
    ],
    email: '',
    emailRules: [
      (v: string) => !!v || 'E-mail is required',
      (v: string) => /.+@.+\..+/.test(v) || 'E-mail must be valid',
    ],
    colors: [
      'red', 'pink', 'purple', 'deep-purple', 'indigo', 'blue', 'light-blue', 'cyan', 'teal', 'green', 'light-green', 'lime', 'yellow', 'amber', 'orange', 'deep-orange', 'brown', 'blue-grey', 'grey'
    ],
    image: undefined as UserImage | undefined,
    color: 'blue-grey' as string,
    showImages: false as boolean,
    showColors: false as boolean,
    file: undefined as File | undefined
  }),
  methods: {
    confirm (): void {
      const pd: PersonData = {
        name: this.name, 
        email: this.email,
        deleted: false,
        isSelf: true
      };
      if (this.color) pd.color = this.color;
      if (this.image) pd.image = this.image.id;
      peopleModule.addPerson(pd).then(() => {
        this.$router.push({ path: '/guide' });
      });
    },
    selectImage(img: UserImage) {
      this.showImages = false;
      this.image = img;
    },
    selectColor(color: string) {
      this.showColors = false;
      this.color = color;
    },
    toggleImages(): void {
      this.showColors = false;
      this.showImages = !this.showImages;
    },
    toggleColors(): void {
      this.showImages = false;
      this.showColors = !this.showColors;
    },
    importData(file: File): void {
      if (file.type && file.type.indexOf('json') === -1) {
        console.log('File is not a json.', file.type, file);
        this.file = undefined;
        return;
      }

      const reader = new FileReader();
      reader.addEventListener('load', async (event) => {
        if (event.target !== null && typeof event.target.result === 'string') {
          const data = JSON.parse(event.target.result) as ApplicationData;
          if (data.acs && data.people) {
            for (let i=0; i<data.people.length; i++)
              await peopleModule.addPerson(data.people[i]);
            for (let i=0; i<data.acs.length; i++)
              await acModule.addAC(data.acs[i]);
            if (data.bonus) {
              localStorage.setItem('bonus', JSON.stringify(data.bonus));
              acModule.loginBonusUpdated();
            }
            this.$router.replace({ path: '/' });
            return;
          } 
        }
        this.file = undefined;
      });
      reader.readAsText(file);
    },
  },
  computed: {
    images(): Array<UserImage> {
      return imageModule.images;
    },
    imageSource(): string {
      return this.image ? require(`@/assets/${this.image.path}`) : '';
    },
  },
  watch: {
    file: function(newFile) {
      if (newFile) this.importData(newFile);
    }
  }

});
